/*--------------------------------------------------------------------------------------//
//                                      About.css                                        //
//--------------------------------------------------------------------------------------*/

/* About Section - Main container for the about section */
.about {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: clamp(50vw, 57vw, 59vw);
    min-height: 350px;
    max-height: 700px;
    background-color: var(--app-light-mode-color);
}

/* Dark Mode - Applies dark mode styling to about section */
.about.dark-mode {
    background-color: var(--app-dark-mode-color);
}

/* Header - Title for About section */
.about__header {
    display: flex;
    align-items: center;
    height: clamp(1.3rem, 8vw, 6rem);
    padding: 0;
    opacity: 0;

    font-size: clamp(1.65rem, 4.8vw, 3rem);
    font-weight: 400;
}

/* Dark Mode - Applies dark mode styling to about section header */
.about__header.dark-mode {
    color: white;
}

/* Wrapper - Wrapper for left and right sides of the about page */
.about__wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 78%; /* Increases this moves the pic + speech bubbles down */
    padding-top: clamp(1rem, 1vw, 2rem);
    transform: translateX(1.2rem);
}

/* Left Side - Left part of the about page with picture and title */
.about__left {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-start;
    width: 40%;
    height: 100%;
    z-index: 0;
}

/* Polaroid Wrapper - Wrapper for polaroid wireframe */
.about__polaroid-wrapper {
    display: flex;
    aspect-ratio: 1/1.1;
	margin-right: 1.6vw;
    min-width: clamp(174px, 30vw, 370px);
    opacity: 0; /* Hide until animation */
}
.about__polaroid-wrapper.animate-polaroid-wrapper {
    animation: baseFadeInSlideRight 0.85s ease-out forwards;
}

/* Image Container - Contains the image in the about section */
.about-image-container {
    display: flex;
    height: 100%;
    width: 107%;
    transform: translateX(-5%);

    box-shadow: 5px 5px 10px grey; /* Vertical, horizontal, blur radius */
    background-image: url('../../images/kevin_and_emma.png');
    background-size: cover;
    animation: pop-in 0.52s cubic-bezier(0, 1.05, 0.3, 1) forwards;
}

/* Right Side - Right side of the about page with text bubbles */
.about__right {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 100%;
    width: 60%;
    aspect-ratio: 1/1;

    font-size: clamp(0.7rem, 2.3vw, 1.7rem);

    z-index: 0;
}

/* Typing Dots Wrapper - Container for text bubbles */
.about__typing-dots-wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    transform: translateY(-3%);
    height: 100%;
    width: 100%;
    margin: 0;
    max-width: clamp(230px, 50vw, 600px);
}

/*--------------------------------------------------------------------------------------//
//                                   .Polaroid Code                                     //    
//                      Credit: https://codepen.io/havardob/pen/jOwrXaJ                 //
//--------------------------------------------------------------------------------------*/

/* Custom Font - Polaroid Font */
@font-face {
    font-family: 'sam', sans-serif;
    src: url('../../fonts/Sam.otf') format('truetype');
    font-weight: 400;
    font-style: normal;
}

/* Stack - Container for polaroid cards */
.polaroid__stack {
	background-color: orange;
	width: 85%;
	max-width: 400px;
	transition: .25s ease;
	&:hover {			
		transform: rotate(6deg);
		.card:before {
			transform: translatey(-4%) rotate(-8deg);
		}	
		.card:after {
			transform: translatey(4%) rotate(8deg);
		}
	}
}

/* Card - Individual polaroid card */
.polaroid__card {
	position: relative;
	display: flex;
	flex-direction: column;
	height: 100%;
	padding: 5% 5% 5% 5%;
	border: 2.5px solid black;
	background-color: white;
	transition: .15s ease;
	z-index: auto;
	
    /* Extra polaroid cards behind the main one */
	&:before, &:after {
		position: absolute;
		display: block;
		content: "";
		height: 100%;
		width: 100%;
		background-color: white;
		border: 2.5px solid;
		transform-origin: center center;
		transition: .15s ease;
		top: 0;
		left: 0;
		z-index: -1;
	}
	&:before {
		transform: translatey(-2%) rotate(-6deg);
		z-index: -1;
	}
	&:after {
		transform: translatey(2%) rotate(6deg);
		z-index: -2;
	}
}
/* Card Dark Mode - Styling for dark mode */
.polaroid__card.dark-mode  {
	background-color: var(--app-dark-mode-color);
	border: 2.5px solid white;
	&:before, &:after {
		background-color: black;
		border: 2.5px solid white;
	}
}

/* Image - Polaroid image styling */
.polaroid__image {
	position: relative;
	width: 100%;
	height: 100%;
	border: 2.5px solid black;
	overflow: hidden;
	background: url('../../images/snowflake_animation.gif') center/cover no-repeat, 
	linear-gradient(to bottom, #c6efff, #ffffff);
	background-size: cover;
}
.polaroid__image.dark-mode {
	background: url('../../images/snowflake_animation.gif') center/cover no-repeat, 
		linear-gradient(to bottom, #15009e, #adbaff);
}

/* Caption - Text caption for Polaroid */
.polaroid__caption {
	font-family: sam;
	display: flex;
	align-self: center;
	justify-self: center;
	margin-top: 5.5%;
	margin-bottom: 2%;
	font-size: clamp(1rem, 2.95vw, 1.95rem);
	color: black;
}
.polaroid__caption.dark-mode {
	color: white;
}	
